import React, { useEffect, useState } from 'react';
import { ContextMenuTrigger } from 'react-contextmenu';
import { connect } from 'react-redux';
import { Button, Modal, Row } from 'reactstrap';

import CreateHackathonModal from '../../../components/application/CreateHackathonModal';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import HackCard from '../../../containers/application/HackathonCard';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import IntlMessages from '../../../helpers/IntlMessages';
import { getCurrentUser, log } from '../../../helpers/Utils';

import { getAllHackathons } from '../../../redux/actions';
import ChallengesContextMenu from './components/context-menu';

const DefaultView = ({
  match,
  accessToken,
  organizerHackathons,
  getAllHackathonsAction,
  loading,
}) => {
  const { id } = getCurrentUser();
  console.log(id);
  useEffect(() => {
    getAllHackathonsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [openCreateHackathon, setOpenCreateHackathon] = useState(false);

  const defaultPage = () => {
    if (organizerHackathons.length === 0) {
      return (

        <Row>
          <Colxx xxs="12">
            <h2><b> Your hackathons</b></h2>
            <br />
            <div className="alert alert-primary text-center mb-5 mx-5">
              You have<br /><span className="display-1"><b>0</b></span><br />hackathons created!
            </div>
          </Colxx>
        </Row>
      );
    }
    return null;
  }

  if (loading) return <div className="loading" />;

  const onContextMenuClick = (e, item) => {
    log('onContextMenuClick - action : ', item.action);

    switch (item.action) {
      case 'refresh':
        getAllHackathonsAction(accessToken, id);
        break;

      case 'add-new':
        setOpenCreateHackathon(true);
        break;

      default:
        break;
    }
  };

  return (
    <>
      <Row>
        <Colxx xxs="10">
          <Breadcrumb heading="menu.challenges" match={match} />
        </Colxx>


        <Colxx xxs="2">
          <Button size="sm" onClick={() => setOpenCreateHackathon(true)}>
            <IntlMessages id="challenges.create-hackathon" />
          </Button>

          <Modal
            isOpen={openCreateHackathon}
            toggle={() => setOpenCreateHackathon(!openCreateHackathon)}
            wrapClassName="modal-right"
          >
            <CreateHackathonModal
              setOpenCreateHackathon={setOpenCreateHackathon}
              id={id}
            />
          </Modal>
        </Colxx>
      </Row>

      <Separator className="mb-5" />
      {defaultPage()}

      {/* <pre>{JSON.stringify(organizerHackathons, null, 2)}</pre> */}

      <ContextMenuTrigger id="challenges_menu">
        <Row>
          <Colxx xxs="12" className="mb-4">
            <Row className="justify-content-start">
              {organizerHackathons.map((item) => {
                if (item.name !== undefined) {
                  return <Colxx xxs="12" sm="6" md="4" key={item.id} className="mb-4">
                    <HackCard data={item} showEdit={item.submit_status === 0} />
                  </Colxx>
                }
                return null;
              })}
            </Row>
          </Colxx>
        </Row>
      </ContextMenuTrigger>
      <ChallengesContextMenu onContextMenuClick={onContextMenuClick} />
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { organizerHackathons, loading, error } = hackathonData;
  return { accessToken, organizerHackathons, loading, error };
};

export default connect(mapStateToProps, {
  getAllHackathonsAction: getAllHackathons,
})(DefaultView);
