import React from 'react';
import { ContextMenu, MenuItem } from 'react-contextmenu';

const ChallengesContextMenu = ({ onContextMenuClick }) => {
  return (
    <ContextMenu id="challenges_menu" style={{ borderRadius: '16px' }}>
      <MenuItem onClick={onContextMenuClick} data={{ action: 'refresh' }}>
        <i className="simple-icon-refresh" /> <span> Refresh</span>
      </MenuItem>
      <MenuItem onClick={onContextMenuClick} data={{ action: 'add-new' }}>
        <i className="simple-icon-plus" /> <span> Add Hackathon</span>
      </MenuItem>
    </ContextMenu>
  );
};

export default ChallengesContextMenu;
