import React, { useEffect, useState } from 'react';
import {
  Button,
  CardTitle,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Tooltip,
} from 'reactstrap';
import { megaTrends } from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';
import { Colxx } from '../common/CustomBootstrap';

const AddMegaTrendsModal = ({ setOpenMegaTrends, values, setValues }) => {
  const [checked, setChecked] = useState([]);

  const handleChange = (items) => {
    setChecked(items);
  };

  useEffect(() => {
    setValues({
      ...values,
      mega_trends: megaTrends.filter((x) => checked.includes(x.id)),
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [checked]);

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.add-mega-trends-title" />
        </h2>
      </ModalHeader>
      <ModalBody>
        <Row>
          {megaTrends.map((item) => (
            <MegaTrendItem
              key={item.key}
              item={item}
              checked={checked}
              setChecked={handleChange}
            />
          ))}
        </Row>
      </ModalBody>
      <ModalFooter className="justify-content-end">
        <Button color="secondary" onClick={() => setOpenMegaTrends(false)}>
          <IntlMessages id="challenges.modal.application-add" />
        </Button>
      </ModalFooter>
    </>
  );
};

const MegaTrendItem = ({ item, checked, setChecked }) => {
  const [tooltipOpen, setTooltipOpen] = useState(false);

  const handleChange = (data) => {
    const checkedItems = [...checked];
    const index = checkedItems.indexOf(data);
    if (index === -1) {
      checkedItems.push(data);
    } else {
      checkedItems.splice(index, 1);
    }
    setChecked(checkedItems);
  };

  return (
    <Colxx xxs="6" className="d-flex flex-row mb-4">
      <img
        src={`/assets/img/mega-trends/${item.id}.png`}
        alt={item.name}
        className="img-thumbnail list-thumbnail align-self-center m-4 small"
      />
      <div className="d-flex flex-grow-1 min-width-zero">
        <div className="pl-0 align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero">
          <div className="min-width-zero">
            <CardTitle
              className="truncate text-small"
              id={`mega_trend_tooltip_${item.id}`}
            >
              {item.name}
            </CardTitle>
            <Button
              onClick={() => handleChange(item.id)}
              size="xs"
              color={checked.includes(item.id) ? 'success' : 'primary'}
            >
              {checked.includes(item.id) ? 'Added' : 'Add'}
            </Button>

            <Tooltip
              placement="top"
              isOpen={tooltipOpen}
              target={`mega_trend_tooltip_${item.id}`}
              toggle={() => setTooltipOpen(!tooltipOpen)}
            >
              {item.name}
            </Tooltip>
          </div>
        </div>
      </div>
    </Colxx>
  );
};

export default AddMegaTrendsModal;

