/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import Proptypes from 'prop-types';
import {
  Badge,
  Button,
  FormGroup,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import Select from 'react-select';
import { connect } from 'react-redux';

import IntlMessages from '../../helpers/IntlMessages';
import CustomSelectInput from '../common/CustomSelectInput';
import { hackthonTypeOptions } from '../../constants/defaultValues';
import AddSDGModal from './AddSDGModal';
import AddMegaTrendsModal from './AddMegaTrendsModal';
import { getAllHackathons } from '../../redux/actions';
import { NotificationManager } from '../common/react-notifications';

import useRequest from '../../hooks/use-request';
import { wait } from '../../helpers/Utils';

const INIT_STATE = {
  name: '',
  hackathon_type_id: 1,
  sdg_goals: [],
  mega_trends: [],
};

const CreateHackathonModal = ({
  setOpenCreateHackathon,
  id,
  accessToken,
  refresh,
}) => {
  const [openAddSdg, setOpenAddSdg] = useState(false);
  const [openMegaTrends, setOpenMegaTrends] = useState(false);

  const [values, setValues] = useState({ ...INIT_STATE, organizer_id: id });

  const [selectedOption, setSelectedOption] = useState({
    label: 'Blender',
    value: 1,
    key: 1,
  });

  const [doRequest, loading] = useRequest({
    url: '/createHackathon',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Hackathon created successfully!',
        'Hackathon Create Success',
        3000,
        null,
        null,
        ''
      );
      refresh(accessToken, id);
      setOpenCreateHackathon(false);

      wait(400, () => {
        setValues({ ...INIT_STATE, organizer_id: id });
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Hackathon Create Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event, key, value = false) => {
    setValues({
      ...values,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = () => {
    doRequest();
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.modal-title" />
        </h2>
        <p className="m-0">
          <IntlMessages id="challenges.modal.modal-sub-title" />
        </p>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.name" />
          </Label>
          <Input
            type="text"
            className="form-control"
            name="link-to-code-of-control"
            placeholder="Enter a Name"
            value={values.name}
            onChange={(event) => {
              handleChange(event, 'name');
            }}
          />
        </FormGroup>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.hackathon-type" />
          </Label>
          <Select
            components={{ Input: CustomSelectInput }}
            className="react-select"
            classNamePrefix="react-select"
            name="hackathon-type-id"
            value={selectedOption}
            onChange={(data) => {
              setSelectedOption(data);
              console.log(data);
              handleChange(data.value, 'hackathon_type_id', true);
            }}
            options={hackthonTypeOptions}
            placeholder="Enter a Hackthon Type"
          />
        </FormGroup>
        <FormGroup className="text-center">
          <Button onClick={() => setOpenAddSdg(true)} size="xs" className="m-2">
            <i className="iconsminds-add mr-2" aria-hidden="true" />
            <IntlMessages id="challenges.modal.add-sdg-button" />
          </Button>

          <Button
            onClick={() => setOpenMegaTrends(true)}
            size="xs"
            className="m-2"
          >
            <i className="iconsminds-add mr-2" aria-hidden="true" />
            <IntlMessages id="challenges.modal.add-mega-trends-button" />
          </Button>
        </FormGroup>

        <FormGroup>
          {values.sdg_goals.length ? (
            <>
              <Label>SDG Goals</Label>
              <div className="d-flex justify-content-around flex-wrap">
                {values.sdg_goals.map((item) => (
                  <Badge
                    pill
                    color="outline-primary"
                    key={item.id}
                    className="mb-2"
                  >
                    {item.name}
                  </Badge>
                ))}
              </div>
            </>
          ) : null}
        </FormGroup>

        <FormGroup>
          {values.mega_trends.length ? (
            <>
              <Label>Mega Trends</Label>
              <div className="d-flex justify-content-around flex-wrap">
                {values.mega_trends.map((item) => (
                  <Badge
                    pill
                    key={item.id}
                    className="mb-2"
                    color="outline-primary"
                  >
                    {item.name}
                  </Badge>
                ))}
              </div>
            </>
          ) : null}
        </FormGroup>

        <Modal
          isOpen={openAddSdg}
          toggle={() => setOpenAddSdg(!openAddSdg)}
          size="lg"
        >
          <AddSDGModal
            setOpenAddSdg={setOpenAddSdg}
            values={values}
            setValues={setValues}
          />
        </Modal>
        <Modal
          isOpen={openMegaTrends}
          toggle={() => setOpenMegaTrends(!openMegaTrends)}
          size="lg"
          modalClassName="rounded"
        >
          <AddMegaTrendsModal
            setOpenMegaTrends={setOpenMegaTrends}
            values={values}
            setValues={setValues}
          />
        </Modal>
      </ModalBody>

      <ModalFooter className="justify-content-end">
        <Button
          id="create-hackthon"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="challenges.create-hackathon" />
          </span>
        </Button>

        <Button color="danger" onClick={() => setOpenCreateHackathon(false)}>
          <IntlMessages id="challenges.modal.cancel-button" />
        </Button>
      </ModalFooter>
    </>
  );
};

CreateHackathonModal.proptype = {
  setOpenCreateHackathon: Proptypes.func.isRequired,
};

const mapStateToProps = ({ authUser }) => {
  const { accessToken } = authUser;
  return { accessToken };
};

export default connect(mapStateToProps, {
  refresh: getAllHackathons,
})(CreateHackathonModal);
